@import "../../styles/breakpoints.scss";

// COLORS
$effect-color: #2a96af;
$bgColor: #1f1f1f;
$bgColor2: #374140;

.projects-gallery {
  width: 100%;
  height: 100%;
  position: relative;
}

.project-item {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 100%;
  opacity: 0;
  display: flex;
  visibility: hidden;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 0.5rem;
  padding: 1.5rem;
  transition: all 0.3s linear;
}

.project-imgs {
  width: 100%;
  min-height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;

  & > a {
    display: flex;
    justify-content: center;
    width: 100%;
  }
}

.project-img {
  width: 100%;
  border: 3px solid $effect-color;
  align-self: flex-start;
  display: block;
}

.project-details {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  row-gap: 1.5rem;
}

.project-title {
  text-align: center;
  font-size: 1.5rem;
  color: $effect-color;
}

.project-desc {
  text-align: justify;
  padding: 0rem 0.45rem;
  font-size: 1.2rem;
}

.project-tools {
  flex-grow: 1;
  text-align: center;
  font-size: 1.4rem;
  color: $effect-color;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}

.tools {
  display: flex;
  max-width: 100%;
  column-gap: 0.5rem;
  row-gap: 0.5rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.tool-wrapper {
  width: 65px;
  height: 65px;
  display: flex;
  justify-content: center;
  align-self: center;
}

.tool {
  width: inherit;
  max-height: inherit;
  display: flex;

  & > img {
    width: 100%;
    align-self: flex-start;
    display: block;
  }
}

// Animation for Slides
.slide-in {
  left: 0;
  opacity: 1;
  visibility: visible;
}

.slide-out {
  left: -100%;
  opacity: 0;
}

.left-arrow,
.right-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #2a96af;
  font-size: 2rem;
  cursor: pointer;
}

.left-arrow {
  left: 0.2rem;
}

.right-arrow {
  right: 0.2rem;
}

// Tiny mobiles
@include responsive-below-specific(380, 650) {
  .project-desc {
    font-size: 1rem;
  }
  .tool-wrapper {
    width: 35px;
    height: 35px;
  }
}

// Tiny mobile landscape
@media screen and (min-width: 567px) and (max-height: 340px) {
  // @include responsive-landscape(567px, 340px){
  .projects-gallery {
    max-width: 80%;
    max-width: 1500px;
  }

  .project-item {
    flex-direction: row;
    column-gap: 3rem;
  }

  .project-imgs {
    max-height: unset;
  }

  .project-details {
    max-width: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    justify-content: center;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-desc {
    font-size: 1rem;
    margin-top: 0;
  }

  .project-tools {
    flex-grow: 0;
  }

  .tools-title {
    font-size: 1.2rem;
  }

  .tool-wrapper {
    width: 40px;
    height: 40px;
  }
}

// Tablets
@include responsive-above(sm) {
  .projects-gallery {
    width: 80%;
    height: 90%;
  }

  .project-item {
    row-gap: 3rem;
  }

  .project-imgs {
    // min-height: 50%;
    max-height: 40%;
  }

  .project-img {
    max-height: 50%;
  }

  .project-details {
    row-gap: 3rem;
  }

  .project-title {
    font-size: 2rem;
  }

  .project-desc {
    font-size: 1.5rem;
    margin-top: 2rem;
  }

  .project-tools {
    row-gap: 2rem;
  }

  .tools {
    column-gap: 2rem;
  }

  .tools-title {
    font-size: 2rem;
  }

  .left-arrow {
    left: -5%;
  }
  .right-arrow {
    right: -5%;
  }
}

// Tablets Landscape
@media screen and (min-width: 576px) and (max-height: 768px) {
  .projects-gallery {
    max-width: 80%;
    max-width: 1500px;
  }

  .project-item {
    flex-direction: row;
    column-gap: 3rem;
  }

  .project-imgs {
    max-height: unset;
  }

  .project-details {
    max-width: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    justify-content: center;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-desc {
    font-size: 1rem;
    margin-top: 0;
  }

  .project-tools {
    flex-grow: 0;
  }

  .tools-title {
    font-size: 1.2rem;
  }

  .tool-wrapper {
    width: 40px;
    height: 40px;
  }
}

@include responsive-above(md) {
  .project-imgs {
    min-height: unset;
  }
}

@include responsive-above(lg) {
  .projects-gallery {
    max-width: 80%;
    max-width: 1500px;
  }

  .project-item {
    flex-direction: row;
    column-gap: 3rem;
  }

  .project-imgs {
    max-height: unset;
  }

  .project-details {
    max-width: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    justify-content: center;
  }

  .project-desc {
    margin-top: 0;
  }

  .project-tools {
    flex-grow: 0;
  }
}
