@import '../../../styles/breakpoints.scss';

$main: #2a96af;

.about__content{
    width: 96%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.about__title{
    text-align: center;
    min-height: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.about__text{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow: hidden;
    position: relative;
}

.ballon{
    text-align: justify;
    padding: 0.7rem;
    background-color: rgb(26, 26, 26);
    position: absolute;
    top: 75%;
    min-height: 50%;
    display: flex;
    align-items: center;
    bottom: auto;
    z-index: -1;
    opacity: 0;
    transition: all 0.6s ease-out;
}

.ballon:nth-child(odd){
    border-left: 6px solid rgb(29, 103, 138);
}

.ballon:nth-child(even){
    border-right: 6px solid rgb(29, 103, 138);
}


.ballon-prev{
    top: calc(-35% - 1rem);
    opacity: 0.3;
    z-index: -2; 
}

.ballon-curr{
    opacity: 1;
    top: 15%;
}

.ballon-next{
    top: calc(65% + 1rem);
    opacity: 0.3;
    z-index: -2;
}

.resume__container{
    height: 12%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slide-bts{
    height: 12%;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 1rem;
}

.arrow {
  color: $main;
  font-size: 2.5rem;
  font-weight: bold;
  opacity: 0.6;
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
}

@include responsive-above(md){
    .about__content{
        width: 70%;
        max-width: 1000px;
    }

    .about__title{
        min-height: 20%;
    }

    .ballon{
        padding: 1.6rem 3rem;
        font-size: 1.2rem;
    }

}