.about__content {
  width: 96%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.about__title {
  text-align: center;
  min-height: 10%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.about__text {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  overflow: hidden;
  position: relative;
}

.ballon {
  text-align: justify;
  padding: 0.7rem;
  background-color: #1a1a1a;
  position: absolute;
  top: 75%;
  min-height: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  bottom: auto;
  z-index: -1;
  opacity: 0;
  -webkit-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}

.ballon:nth-child(odd) {
  border-left: 6px solid #1d678a;
}

.ballon:nth-child(even) {
  border-right: 6px solid #1d678a;
}

.ballon-prev {
  top: calc(-35% - 1rem);
  opacity: 0.3;
  z-index: -2;
}

.ballon-curr {
  opacity: 1;
  top: 15%;
}

.ballon-next {
  top: calc(65% + 1rem);
  opacity: 0.3;
  z-index: -2;
}

.resume__container {
  height: 12%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.slide-bts {
  height: 12%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.arrow {
  color: #2a96af;
  font-size: 2.5rem;
  font-weight: bold;
  opacity: 0.6;
  cursor: pointer;
}

.arrow:hover {
  opacity: 1;
}

@media (min-width: 991px) {
  .about__content {
    width: 70%;
    max-width: 1000px;
  }
  .about__title {
    min-height: 20%;
  }
  .ballon {
    padding: 1.6rem 3rem;
    font-size: 1.2rem;
  }
}
