.menu {
  position: fixed;
  right: 0;
  bottom: 0;
  height: 20vh;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  z-index: 10;
}

.links {
  margin: auto;
  position: absolute;
  width: 0;
  bottom: 7rem;
  right: 3rem;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
  overflow: hidden;
  padding-bottom: 0.3rem;
}

.links_inner {
  position: relative;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  opacity: 0;
  row-gap: 0.6rem;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

.links_inner > a {
  width: calc(100% - 0.3rem);
  background-color: #2a96af;
  padding: 0.2rem 0.6rem;
  border-radius: 1rem;
  border: 0.3px solid silver;
  color: white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  word-wrap: unset;
  white-space: nowrap;
  cursor: pointer;
  -webkit-transition: all 0.3s ease, opacity 0.4s ease, width 0.1s ease;
  transition: all 0.3s ease, opacity 0.4s ease, width 0.1s ease;
}

.links_inner > a:hover {
  -webkit-box-shadow: 0.05rem 0.05rem 0.1rem #ffffff;
          box-shadow: 0.05rem 0.05rem 0.1rem #ffffff;
  outline: none;
}

.move-left {
  width: 7.5rem;
}

.expand {
  opacity: 1 !important;
  color: transparent;
}

.round-menu {
  position: absolute;
  right: 3rem;
  bottom: 3rem;
  width: 3rem;
  height: 3rem;
}

@media (max-width: 768px) {
  .links {
    bottom: 5.5rem;
    right: 1.5rem;
  }
  .round-menu {
    right: 1.5rem;
    bottom: 1.5rem;
  }
}

.inner {
  width: 100%;
  height: 100%;
}

.circle {
  position: relative;
  width: inherit;
  height: inherit;
  background-color: transparent;
  border: 3px solid #2a96af;
  border-radius: 50%;
  display: none;
  -webkit-animation: twinkle 2s infinite;
          animation: twinkle 2s infinite;
  cursor: pointer;
  opacity: 0.3;
}

.circle:hover {
  opacity: 1;
  -webkit-animation: unset;
          animation: unset;
}

.circle .inner_circle {
  position: absolute;
  border-radius: inherit;
  background-color: #2a96af;
  top: 10%;
  right: 10%;
  bottom: 10%;
  left: 10%;
}

@-webkit-keyframes twinkle {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.3;
  }
}

@keyframes twinkle {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.3;
  }
}

.x-btn {
  width: 50px;
  height: 50px;
  display: none;
  cursor: pointer;
}

.x-btn:hover > * {
  -webkit-transform: translate(-50%, -50%) rotate(90deg);
          transform: translate(-50%, -50%) rotate(90deg);
  height: 50%;
  width: 50%;
  border-radius: 50%;
}

.line1 {
  height: inherit;
  width: 3px;
  background-color: #2a96af;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  -webkit-transition: 1.2s ease;
  transition: 1.2s ease;
}

.line2 {
  height: inherit;
  width: 3px;
  background-color: #2a96af;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
  -webkit-transition: 1.2s ease;
  transition: 1.2s ease;
}

.goto-arrow {
  position: absolute;
  bottom: 3rem;
  left: 3rem;
}

.inner_arrow {
  position: relative;
  width: 3rem;
  height: 3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.show {
  display: block;
}

.reveal {
  opacity: 1 !important;
}
