@import "../../styles/breakpoints.scss";

// COLORS
$main: #2a96af;
$bg-color: #1f1f1f;
$bg-color2: #374140;
$text-color: #ffffff;
$effect-color: #f26101;

.menu {
  position: fixed;
  right: 0;
  bottom: 0;
  height: 20vh;
  width: fit-content;
  z-index: 10;
}

// TRIAL NEW WAY
.links {
  margin: auto;
  position: absolute;
  width: 0;
  bottom: 7rem;
  right: 3rem;
  transition: all 0.5s ease-in;
  overflow: hidden;
  padding-bottom: 0.3rem;
}


.links_inner {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  opacity: 0;
  row-gap: 0.6rem;
  transition: all 0.5s ease-in;

  & > a {
    width: calc(100% - 0.3rem);
    background-color: $main;
    padding: 0.2rem 0.6rem;
    border-radius: 1rem;
    border: 0.3px solid silver;
    color: white;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    word-wrap: unset;
    white-space: nowrap;
    cursor: pointer;
    transition: all 0.3s ease, opacity 0.4s ease, width 0.1s ease;

    &:hover {
      box-shadow: 0.05rem 0.05rem 0.1rem #ffffff;
      outline: none;
    }
  }
}

.move-left {
  width: 7.5rem;
}

.expand {
  opacity: 1 !important;
  color: transparent;
}

.round-menu {
  position: absolute;
  right: 3rem;
  bottom: 3rem;
  width: 3rem;
  height: 3rem;
}

@include responsive-below(sm) {
  .links{
    bottom: 5.5rem;
    right: 1.5rem;
  }

  .round-menu {
    right: 1.5rem;
    bottom: 1.5rem;
  }
}

.inner {
  width: 100%;
  height: 100%;
}

// OPEN BUTTON FOR MENU
.circle {
  position: relative;
  width: inherit;
  height: inherit;
  background-color: transparent;
  border: 3px solid $main;
  border-radius: 50%;
  display: none;
  animation: twinkle 2s infinite;
  cursor: pointer;
  opacity: 0.3;

  &:hover {
    opacity: 1;
    animation: unset;
  }

  .inner_circle {
    position: absolute;
    border-radius: inherit;
    background-color: $main;
    top: 10%;
    right: 10%;
    bottom: 10%;
    left: 10%;
  }
}

@keyframes twinkle {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.3;
  }
}

// CLOSE BUTTON FOR MENU

.x-btn {
  width: 50px;
  height: 50px;
  display: none;
  cursor: pointer;
}

.x-btn:hover > * {
  transform: translate(-50%, -50%) rotate(90deg);
  height: 50%;
  width: 50%;
  border-radius: 50%;
}

.line1 {
  height: inherit;
  width: 3px;
  background-color: $main;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: 1.2s ease;
}

.line2 {
  height: inherit;
  width: 3px;
  background-color: $main;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: 1.2s ease;
}

// ARROWS TO GO TO PROJECTS
.goto-arrow {
  position: absolute;
  bottom: 3rem;
  left: 3rem;
}

.inner_arrow {
  position: relative;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}



// HELPER CSS CLASS
.show {
  display: block;
}

.reveal {
  opacity: 1 !important;
}
