.notice {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.social-text {
  -webkit-transition: all linear 0.3s;
  transition: all linear 0.3s;
  padding: 0.5rem;
  border-radius: 1rem;
  cursor: pointer;
}

.social-text > a {
  text-decoration: none;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
}

.social-text > a > div {
  text-align: center;
}

.social-icon {
  font-size: 2rem;
}

.social-name {
  font-size: 1.5rem;
  font-weight: bold;
}

.footer__page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  row-gap: 2rem;
}

@media (max-width: 768px) {
  .social-text {
    height: 100%;
  }
  .social-text > a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .social-icon {
    min-width: 2.3rem;
  }
  #linkedin > .social-icon {
    color: #0a66c2;
  }
  #git > .social-icon {
    color: #6f42c1;
  }
  #fcc > .social-icon {
    color: #0a0a23;
  }
  #email > .social-icon {
    color: #2a96af;
  }
}

@media (min-width: 767px) {
  .social-text:hover {
    background-color: white;
  }
  .social-text:hover > #linkedin {
    color: #0a66c2;
  }
  .social-text:hover > #git {
