* {
  box-sizing: border-box;
  font-family: "Poppins", Verdana, Geneva, Tahoma, sans-serif;
}

html {
  overflow: hidden;
}
body{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  height: -webkit-fill-available !important;
}


.full-page {
  position: relative;
  width: inherit;
  height: inherit;
}