$breakpoints: (
  xs: 576px,
  sm: 768px,
  md: 992px,
  lg: 1200px,
);

@mixin responsive-wh-upto($w-breakpoint, $h-breakpoint) {
  @if map-has-key($map: $breakpoints, $key: $w-breakpoint) and
    map-has-key($map: $breakpoints, $key: $h-breakpoint)
  {
    // Get values to create media query
    $w-breakpoint-value: map-get(
      $map: $breakpoints,
      $key: $w-breakpoint,
    );
    $h-breakpoint-value: map-get(
      $map: $breakpoints,
      $key: $h-breakpoint,
    );

    @media (max-width: $w-breakpoint-value) and (max-height: $h-breakpoint-value) {
      @content;
    }
  } @else {
    @warn ("The breakpoint: #{breakpoint} does not exist.");
  }
}

@mixin responsive-landscape($w-breakpoint, $h-breakpoint) {
  // For landscape options min-width and a max-height
  @media (min-width: $w-breakpoint) and (max-height: $h-breakpoint) {
    @content;
  }
}

@mixin responsive-below-specific($w-breakpoint, $h-breakpoint) {
  // Specific width and height
  @if ($w-breakpoint < $h-breakpoint) {
    @media (max-width: $w-breakpoint + "px") and (max-height: $h-breakpoint + "px") {
      @content;
    }
  } @else {
    @warn ("The viewport is on landscape, this method is not for this. Check your values");
  }
}

@mixin responsive-below($breakpoint) {
  @if map-has-key($map: $breakpoints, $key: $breakpoint) {
    $breakpoint-value: map-get(
      $map: $breakpoints,
      $key: $breakpoint,
    );
    @media (max-width: $breakpoint-value) {
      @content;
    }
  } @else {
    @warn ("The breakpoint: #{breakpoint} does not exist.");
  }
}

@mixin responsive-above($breakpoint) {
  @if map-has-key($map: $breakpoints, $key: $breakpoint) {
    $breakpoint-value: map-get(
      $map: $breakpoints,
      $key: $breakpoint,
    );
    @media (min-width: ( $breakpoint-value) - 1) {
      @content;
    }
  } @else {
    @warn ("The breakpoint: #{breakpoint} does not exist.");
  }
}

@mixin responsive-between($lower, $upper) {
  @if (map-has-key($map: $breakpoints, $key: $lower)) and
    (map-has-key($map: $breakpoints, $key: $upper))
  {
    $min-value: map-get(
      $map: $breakpoints,
      $key: $lower,
    );
    $max-value: map-get(
      $map: $breakpoints,
      $key: $upper,
    );

    @media (min-width: $min-value) and (max-width: $max-value) {
      @content;
    }
  } @else if (map-has-key($map: $breakpoints, $key: $lower)) {
    @warn ("the breakpoint: #{lower} does not exist.");
  } @else if (map-has-key($map: $breakpoints, $key: $upper)) {
    @warn ("the breakpoint: #{upper} does not exist.");
  }
}
