@import "../../../styles/breakpoints.scss";

// COLORS
$LINKEDIN_BLUE: rgb(10, 102, 194);
$GITHUB_PURPLE: #6f42c1;
$FCC_BLUE: rgb(10, 10, 35);
$effect-color: #2a96af;

.notice {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social {
  display: flex;
  column-gap: 1rem;
  flex-direction: column;
}
.social-text {
  transition: all linear 0.3s;
  padding: 0.5rem;
  border-radius: 1rem;
  cursor: pointer;
  & > a {
    text-decoration: none;
    color: white;
    display: flex;
    column-gap: 0.5rem;

    & > div {
      text-align: center;
    }
  }
}

.social-icon {
  font-size: 2rem;
}

.social-name {
  font-size: 1.5rem;
  font-weight: bold;
}

.footer__page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 2rem;
}

@include responsive-below(sm) {
  .social-text {
    height: 100%;
    & > a {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .social-icon {
    min-width: 2.3rem;
  }

  #linkedin > .social-icon {
    color: $LINKEDIN_BLUE;
  }

  #git > .social-icon {
    color: $GITHUB_PURPLE;
  }

  #fcc > .social-icon {
    color: $FCC_BLUE;
  }

  #email > .social-icon {
    color: $effect-color;
  }
}

@include responsive-above(sm) {
  .social-text {
    &:hover {
      background-color: white;

      & > #linkedin {
        color: $LINKEDIN_BLUE;
      }

      & > #git {
        color: $GITHUB_PURPLE;
      }

      & > #fcc {
        color: $FCC_BLUE;
      }

      & > #email {
        color: $effect-color;
      }
    }
  }
}

// Tiny mobiles landscape
@media screen and (min-width: 567px) and (max-height: 376px) {
  .social {
    flex-direction: row;
  }
  .social-icon{
    font-size: 1.5rem;
  }
  .social-name{
    font-size: 1rem;
  }
}


// Mobiles and Tablets Landscape
@media screen and (min-width: 576px) and (max-height: 768px) {
  .social-text{
    &:hover{
      transform: none;
    }
  }
}

@include responsive-above(md){
  .social-text{
    &:hover{
      transform: none;
      transform: translate(0, 20%);
    }
  }

  .social{
    flex-direction: row;
  }
}
