@import "../../styles/breakpoints.scss";

$main: #f18f01;
$bg-color: #1f1f1f;

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

.book {
  margin: 0;
  padding: 0;
  position: relative;
}

.pages-container {
  width: 100vw;
  position: relative;
}

.page {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: all 1.5s ease;
  transform: translateY(100%);
}

/* scrolled into view */
.scrolled-in {
  transform: translateY(0%);
}

/* scrolled out of view */
.scrolled-out {
  transform: translateY(-100%);
}

.scrollBar {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  display: none;
  visibility: hidden;
  opacity: 0;
  flex-direction: column;
  justify-content: space-between;
  z-index: 100;
  transition: opacity 0.5s linear;
}

.scrollBar:hover {
  cursor: pointer;
}

.up-arrow,
.down-arrow {
  width: 1rem;
  height: 1rem;
  background-color: black;
  cursor: pointer;
}

.bar {
  flex-grow: 2;
  width: 1rem;
  background-color: transparent;
  position: relative;
}

.bar-block {
  width: 1rem;
  background-color: lightsalmon;
  position: absolute;
  top: 0;
  transition: all 0.5s linear;
}

.reveal {
  opacity: 1;
}


